<template>
  <v-responsive class="overflow-y-auto" max-height="500">
    <v-card outlined class="container">
      <v-row justify="center">
        <v-col
          cols="12"
          sm="6"
          v-for="asignacion in asignaciones"
          :key="asignacion.id"
        >
          <v-card outlined>
            <v-card-title class="grey lighten-2">
              Inmueble: {{ asignacion.inmueble.name }} ({{
                asignacion.inmueble.key
              }})
              <v-spacer></v-spacer>
              <confirmar
                mensaje="Estás a punto de eliminar la asignación junto con todos sus trabajos."
                :indice="asignaciones.indexOf(asignacion)"
                :tipo_pago="'¿Confirmar eliminacion ' + asignacion.inmueble.name + '?'"
                @emitConfirmacion="confirmarRecurso"
                icon="mdi-delete"
                color="error"
              />
            </v-card-title>
            <v-simple-table dense>
              <template v-slot:default>
                <thead>
                  <tr>
                    <th>Concepto</th>
                    <th>Asignado</th>
                    <th>Estado</th>
                    <th>Mensaje</th>
                    <th>Confirmar</th>
                    <th>Eliminar</th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    v-for="(trabajo, index) in asignacion.trabajos"
                    :key="index"
                  >
                    <td>{{ trabajo.concepto.name }} ({{ trabajo.concepto.project }})</td>
                    <td>{{ trabajo.usuario_asignado.name }}</td>
                    <td>{{trabajo.estado.name}}</td>
                    <td><span :class="trabajo.mensaje === 'A este inmueble ya se le asignó este trabajo.' ? 'warning' : 'success'">{{ trabajo.mensaje}}</span></td>
                    <td>
                      <v-btn icon x-small
                        ><v-icon>{{
                          trabajo.confirmacion ? "mdi-check" : "mdi-close"
                        }}</v-icon></v-btn
                      >
                    </td>
                    <td>
                      <confirmar
                        :mensaje="'Estás a punto de eliminar el trabajo ' + trabajo.concepto.name + ' del inmueble ' + asignacion.inmueble.name + '.'"
                        :id="index"
                        :indice="asignaciones.indexOf(asignacion)"
                        :tipo_pago="'¿Confirmar eliminacion ' + trabajo.concepto.name + '?'"
                        @emitConfirmacion="confirmarTrabajo"
                        icon="mdi-close"
                        color="error"
                      />
                    </td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </v-card>
        </v-col>
      </v-row>
    </v-card>
  </v-responsive>
</template>
<script>
import confirmacion from "../alertas/Confirmar.vue"
export default {
  components: {
    "confirmar": confirmacion
  },
  computed: {
    asignaciones() {
      return this.$store.getters["trabajo/getAsignacionesVerificadas"];
    },
    inmueblesSeleccionados() {
      return this.$store.getters["inmueble/getInmueblesSeleccionados"];
    },
  },
  methods: {
    confirmarRecurso({confirmacion, indice}){
      if(confirmacion){
        this.$store.commit("trabajo/spliceAsignacionVerificada", indice)
      }
    },
    confirmarTrabajo({confirmacion, indice, id}){
      if(confirmacion){
        this.$store.commit("trabajo/spliceTrabajoAsignacionVerificada", {indice_asignacion: indice, indice_trabajo: id})
      }
    }
  }
};
</script>
