import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify'

import { INICIALIZAR_FUNCIONES, LOGOUT_FUNCTION } from './store/constants';
import { modulesToInit, modulesWithLogoutAction } from './store/modules';

Vue.config.productionTip = false

modulesToInit.forEach((module) => {
  store.dispatch(`${module}/${INICIALIZAR_FUNCIONES}`)
});

new Vue({
  router,
  store,
  vuetify,
  created(){
    modulesWithLogoutAction.forEach((module) => {
      this.$store.dispatch(`${module}/${LOGOUT_FUNCTION}`);
    });
  },
  render: h => h(App)
}).$mount('#app')
